/* hamburgerMenu */
@media only screen and (max-width: 1000px) {
  .hamburgerMenu {
    position: fixed;
    right: -70%;
    width: 50%;
    height: 70vh;
    top: 0;
    display: flex;
    flex-direction: column;
    color: #553300;
    transition: 0.3s;
    background-color: rgba(255, 255, 255, 0.85);
  }
  .hamburgerMenu.open {
    padding: 8% 0 0 0;
    right: 0;
  }
  .header-menu {
    margin: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
  }
}

.hamburger-button {
  padding: 0;
  box-sizing: border-box;
  width: 36px;
  height: 36px;
  z-index: 10;
  border: none;
  background-color: transparent;
}

.hamburger-button-line {
  display: block;
  position: relative;
  width: 100%;
  height: 4px;
  background-color: #553300;
  transition: 0.2s;
}

.hamburger-button-line::before,
.hamburger-button-line::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0px;
  background-color: #553300;
  transition: 0.5s;
}

::before,
::after {
  box-sizing: inherit;
}

.hamburger-button-line::before {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.hamburger-button-line::after {
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

.hamburger-button-line.open {
  background-color: transparent;
}

.hamburger-button-line.open::before,
.hamburger-button-line.open::after {
  background-color: transparent;
  content: "";
  background-color: #553300;
  transition: 0.2s;
}

.hamburger-button-line.open::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.hamburger-button-line.open::after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
